import React, { useState, useEffect } from 'react'
import '../style/RecordForm.css'
import axios from 'axios'

function IncomeForm({ userId, counter, result, setResult }) {
    const [catData, setCat] = useState([[], []])
    const [acData, setAc] = useState([[], []])
    const [date, setDate] = useState(new Date().setDate(new Date().getDate() + counter))
    // const [balance, setbalance] = useState()
    const [formData, setFormData] = useState({
        "userId": userId,
        "account": '',
        "title": '',
        "amount": '',
        "transactionDate": new Date(),
        "description": '',
        "category": '',
        "rescievedFrom": "",
    });
    const handleInputChange = (e) => {
        const { name, value } = e.target;

        setFormData({ ...formData, [name]: value });
    };

    useEffect(() => {
        axios.get('/api/account/name/' + userId,{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        })
            .then((doc) => {
                setAc(doc.data);
            }).catch((err) => {
                console.log(err);
            })
        axios.get('/api/IC',{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        })
            .then((doc) => {
                setCat(doc.data);
            }).catch((err) => {
                console.log(err);
            })
        axios.get('/api/user/'+userId,{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        })
            .then((doc) => {
                setFormData({...formData,account:doc.data.defaultAccount});
            }).catch((err) => {
                console.log(err);
            })
    }, [userId]);

    // useEffect(() => {
    //     if (formData.account) {
    //         axios.get('http://localhost:8080/account/balance/' + userId + '/' + formData.account)
    //             .then((doc) => {
    //                 setbalance(Number(doc.data.currentBalance) + Number(formData.amount))
    //             }).catch((err)=>{
    //                 console.log(err)
    //             })
    //     }
    // }, [formData.account, formData.amount])

    useEffect(() => {
        setDate(new Date().setDate(new Date().getDate() + counter))
        setFormData({
            ...formData,
            transactionDate: date
        })
    }, [counter])

    const handleSubmit = async () => {
        
        if (formData.account === '' || formData.amount === '' || formData.title === '') {
            setResult({warning:'Enter proper data',color:'red'})
        } else {
            await axios.post('/api/income',formData,{
                headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
            })
            .then((doc)=>{
                setResult({warning: 'Income Record added with transaction ID '+doc.data._id,color:'skyblue'})
                
            }).then(()=>{
                // axios.post('/account/update/balance',{
                //     'userId':formData.userId,
                //     'accountId':formData.account,
                //     'balance':balance
                // }).then((doc)=>{
                //     if(doc.data.modifiedCount){
                //         formData.account = ''
                //         formData.amount = ''
                //         formData.title = ''
                //         formData.description = ''
                //         formData.category = ''
                //     } 
                // }).catch((err)=>{
                //     console.log(err)
                // })
            })
            .catch((err)=>{
                console.log(err)
            })
        }
        setTimeout(() => {
            setResult({warning:'',color:'skyblue'})
        }, 10000)
    }
    return (
        <>
            <div className='record-form'>
                <input required type='text' name='title' onInput={handleInputChange} className='form-field' placeholder='Enter Title' value={formData.title}></input>
                <input required type='number' name='amount' onInput={handleInputChange} className='form-field' placeholder='Amount' value={formData.amount}></input>
                <select required type='text' name='category' onInput={handleInputChange} className='form-field' value={formData.category}>
                    <option className='option' value="">Select a Category</option>
                    {catData.map((row) => {
                        return (<option key={row.categoryID} value={row.categoryID} className='option'>{row.categoryName}</option>)
                    })}
                </select>
                <select required type='text' name='account' onInput={handleInputChange} className='form-field' value={formData.account}>
                    <option className='option' value="">Select an Account</option>
                    {acData.map((row) => {
                        return (<option key={row.accountId} value={row.accountId} className='option'>{row.accountName}</option>)
                    })}
                </select>
                <input type='text' name='description' onInput={handleInputChange} className='form-field' placeholder='Description' value={formData.description}></input>
                <button className='record-btn' placeholder='Recieved From' value='Submit' onClick={handleSubmit} ><img width='40px' src={'/public/submit.png'} alt='submit' /></button>
            </div>
            <div className='remark' style={{ color: result.color }}>{result.warning}</div>
        </>
    )
}
export default IncomeForm;