import React, { useEffect, useState } from 'react'
import "../style/BalanceBar.css"
import axios from 'axios'

function BalanceBar({ userId }) {
    const [accountList, setAc] = useState([])
    const [total, setTotal] = useState(0)
    useEffect(() => {
        axios.get('/api/account/'+userId,{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        })
        .then((doc)=>{
            setAc(doc.data)
            setTotal(doc.data.map((curr)=>curr.currentBalance).reduce((sum,curr)=>sum+curr))
        }).catch((err)=>{
            console.log(err)
        })

    }, [userId]);

    return (
        <div className="profile-box">
            <div className='profile-title'>Balance</div>
            <div id='accout-balance'>
                {accountList.map((row) => {
                    return (
                        <div className='account-row'>
                            <span className='account-name'>{row.accountName}</span>
                            <span className='account-balance' style={{color:(row.currentBalance>0)?'rgb(3, 255, 3)':'rgb(255, 47, 0)'}}>{'₹ '+row.currentBalance}</span>
                        </div>
                    )
                })}
                <div className='account-row' id='total'>
                    <span className='account-name'>Total Balance</span>
                    <span className='account-balance' style={{color:'rgb(3, 255, 3)'}}>{'₹ '+total}</span>
                </div>
            </div>
        </div>
    )
}
export default BalanceBar;