import React, { useState, useEffect } from 'react'
import axios from 'axios'
import '../style/Income.css'
import '../style/Record.css'
import '../style/Daily.css'

function Income({ counter, inTotal, userId, result }) {
    const [inData, setData] = useState()
    const [loader, setLoader] = useState(<div class="loader"></div>)
    const [acData,setAc] = useState([{}])
    useEffect(() => {
        axios.get('/api/account/name/' + userId,{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        })
            .then((doc) => {
                setAc(doc.data);
            }).catch((err) => {
                console.log(err);
            })
    }, [userId])

    useEffect(() => {
        setLoader(<div class="loader"></div>)
        axios.post('/api/income/get', { userId, counter },{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        })
            .then((doc) => {
                setLoader('')
                setData(doc.data)
            }).catch((err) => {
                setLoader('Unable to fetch!')
                console.log(err)
            })
    }, [counter, result, userId]);

    return (
        <div id='income-box'>
            <div id='income-title'>Total Income<span id='in-total'>{'₹ ' + inTotal}</span></div>
            <div className='record-list'>
                {(inData === undefined) ?
                    <div className='no-record'>{loader}</div> :
                    (inData.length > 0) ?
                        inData.map((row) => {
                            return (
                                <div key={row._id} className='record'>
                                    <div className='record-left'>
                                        <span className='record-first-line'>
                                            <img src={row.category || '/category/IC0.png'} className='record-img' alt={row[5]}/>
                                            <span className='record-title'>{row.title}</span>
                                            <span className='record-ac'>{acData.map((curr)=>{
                                                if(curr.accountId===row.account)
                                                    return curr.accountName
                                            })}</span>
                                        </span>
                                        <div className='record-desc'>{row.description}</div>
                                    </div>
                                    <div>
                                        <div className='record-amount'>{'₹ ' + row.amount}</div>
                                        <div className='record-from'>{(row.receivedFrom) ? 'from ' + row.receivedFrom : ''}</div>
                                    </div>

                                </div>
                            )
                        }) : <div className='no-record'>Enter Income Record</div>}
            </div>
        </div>
    )
}
export default Income;