import React,{useState} from 'react'
import { NavLink } from 'react-router-dom'
import "../style/Nav.css"
import Mobile from './Mobile'

function Nav({ setUserId }) {
    const [isMobileOpen,setMobile] = useState(false)
    const handleOpen=()=>{
        setMobile(true)
    }
    const handleClose=()=>{
        setMobile(false)
    }
    const logout = () => {
        localStorage.removeItem('loginData')
        localStorage.removeItem('token')
        setUserId(null)
    }
    
    if (isMobileOpen) {
        return (<Mobile logout={logout} handleClose={handleClose}/>)
    } else {
        return (
            <div id="navbar">
                
                <div id="left-nav">
                    <img src={'public/favicon.png'} alt='logo' style={{width:'40px'}}></img>
                    MyExpensesManger
                </div>
                <div id="right-nav">
                    <NavLink to='/' >Home</NavLink>
                    <NavLink to='/monthly' >Monthly</NavLink>
                    <NavLink to='/yearly' >Yearly</NavLink>
                    <NavLink to='/charts' >Charts</NavLink>
                    <NavLink to='/profile' >Profile</NavLink>
                    <button onClick={logout} id='logout'>Log Out</button>
                </div>
                <div id="right-nav-mobile">
                    <button onClick={handleOpen}>=</button>
                </div>
            </div>
        )
    }

}

export default Nav;