import React, { useState } from 'react'
import { useRef } from 'react'
import { Link, Navigate } from 'react-router-dom'
import axios from 'axios'
import bcrypt from 'bcryptjs'
import '../style/SignUp.css'
import '../style/loader.css'
import Login from './Login'

const SignUp = ({ setUserId }) => {
    const [userId, setId] = useState(null)
    const [isOTPSent, setOTPSent] = useState(false)
    const [warn, setWarn] = useState('')
    const [OTP, setOTP] = useState()
    const [OTP2, setOTP2] = useState('')
    const [btn, setBtn] = useState('Final Submit');
    const [formData, setFormData] = useState({
        userId: '',
        userName: '',
        password: '',
        passwordTest: '',
        email: '',
        mobile: '',
        profession: '',
        openingBalance: ''
    })
    const handleInput = (e) => {
        const { name, value } = e.target;
        setFormData({ ...formData, [name]: value })
    }
    const handleOTPIn = (e) => {
        setOTP(e.target.value)
    }
    const sendOTP = async () => {
        setOTPSent(true)
        setWarn({ warning: 'OTP is being sent...', color: 'gray' })
        await axios.post('/api/otp', { email: formData.email })
            .then((doc) => {
                setWarn({ warning: 'OTP has been sent.', color: 'gray' })
                console.log(doc.data.otp)
                setOTP2(doc.data.otp)
            }).catch((err) => {
                setWarn({ warning: 'Unable to fetch!', color: 'red' })
                setOTPSent(false)
                console.log(err)
            })
    }
    const handleOTP = async () => {
        if (formData.userId === '' || formData.userName === '' || formData.email === '' || formData.mobile === '' || formData.password === '' || formData.openingBalance === '') {
            setWarn({ warning: 'Enter Proper Data!', color: 'red' })
        } else if (formData.password !== formData.passwordTest) {
            setWarn({ warning: 'Passwords do not match!', color: 'red' })
        } else if (formData.mobile.length !== 10) {
            setWarn({ warning: 'Enter valid phone number!', color: 'red' })
        } else {
            await axios.get('/api/signup/' + formData.userId)
                .then((doc) => {
                    if (!doc.data.isValid) {
                        setWarn({ warning: 'User ID is not avilabe. Please create another one.', color: 'red' })
                    }
                })
            await axios.get('/api/signup/email/' + formData.email)
                .then((doc) => {
                    if (!doc.data.isValid) {
                        setWarn({ warning: 'There is already an account with this email. Kindly login', color: 'red' })
                    } else {
                        sendOTP();
                    }
                })

        }
    }
    const handleSubmit = async () => {
        if (!bcrypt.compareSync(OTP,OTP2.toString())) {
            setOTPSent(false)
            setWarn('OTP does not match!')
        } else {
            setBtn(<div class="loader"></div>)
            await axios.post('/api/signup', formData)
            .then((doc) => {
                setId(formData.userId);
                localStorage.setItem('token',doc.data.token);
            }).catch((err) => {
                    setBtn('Final Submit')
                    setWarn({ warning: 'Unable to fetch!', color: 'red' });
                    console.log(err);
                })
        }

    }
    if(userId){
        setUserId(userId)
        return <Navigate to={'/'}></Navigate>
    }
    return (
        <div id='signup-outer-box'>
            <div id='signup-box'>
                <div id='signup-title'>User Registration</div>
                <div id='signup-form-box'><form>
                    <input required type='text' className='signup-form-fields' name='userId' placeholder='Create Your Unique User ID' onInput={handleInput}></input>
                    <input required type='text' className='signup-form-fields' name='userName' placeholder='Enter your name' onInput={handleInput}></input>
                    <input required type='password' className='signup-form-fields' name='password' placeholder='Create 8-digit password' onInput={handleInput}></input>
                    <input required type='password' className='signup-form-fields' name='passwordTest' placeholder='Re-enter password' onInput={handleInput}></input>
                    <input required type='email' className='signup-form-fields' name='email' placeholder='Enter Email' onInput={handleInput}></input>
                    <input type='number' className='signup-form-fields' name='mobile' placeholder='Enter 10-digit phone number' onInput={handleInput}></input>
                    <input type='text' className='signup-form-fields' name='profession' placeholder='Enter Profession' onInput={handleInput}></input>
                    <input required type='number' className='signup-form-fields' name='openingBalance' placeholder='Enter Opening Balance' onInput={handleInput}></input></form>
                    <input required type='text' className='signup-form-fields' name='otp' placeholder='Enter OTP' onInput={handleOTPIn} style={{ display: (isOTPSent) ? 'block' : 'none' }}></input>
                    <div id='signup-warn' style={{ color: warn.color }}>{warn.warning}</div>
                    <div>
                        <button id='send-otp-btn' onClick={handleOTP} style={{ display: (isOTPSent) ? 'none' : 'inline' }}>{'Send OTP'}</button>
                        <button id='send-otp-btn' onClick={handleSubmit} style={{ display: (isOTPSent) ? 'inline' : 'none' }}>{btn}</button>
                    </div>
                    <br></br>
                    <Link to={'/login'} element={<Login setUserId={setUserId} />} className="other-details">Already registered? <i>Login Here</i></Link>
                </div>
            </div>
        </div>
    )
}

export default SignUp;