import React, { useEffect, useState } from 'react'
import axios from 'axios'

function UserData({ userId }) {
    const [userData, setUserData] = useState({})

    useEffect(() => {
        axios.get('/api/user/' + userId,{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        })
            .then((doc) => {
                setUserData({ ...doc.data, registrationDate: new Date(doc.data.registrationDate).toLocaleDateString() });
            }).catch((err) => {
                console.log(err);
            })
    }, [userId])
    return (
        <div className='profile-box'>
            <div className='profile-title'>User Profile</div>
            <div id='user-data'>
                <div className='profile-row'><label for='id'>User ID:</label><input type='text' id='id' value={userData.userId}></input></div>
                <div className='profile-row'><label for='name'>User Name:</label><input type='text' id='name' value={userData.userName}></input></div>
                <div className='profile-row'><label for='email'>Email:</label><input type='text' id='email' value={userData.email}></input></div>
                <div className='profile-row'><label for='mobile'>Mobile:</label><input type='text' id='mobile' value={userData.mobile}></input></div>
                <div className='profile-row'><label for='ac'>Default Account:</label><input type='text' id='ac' value={userData.defaultAccount}></input></div>
                <div className='profile-row'><label for='proff'>Proffession:</label><input type='text' id='proff' value={userData.profession}></input></div>
                <div className='profile-row'><label for='doj'>Date of Registraion:</label><input type='text' id='doj' value={userData.registrationDate}></input></div>
            </div>
        </div>
    )
}
export default UserData;