import React from 'react'
import '../style/Error.css'
function Error() {
    return (
        <div id='err-outer-box'>
            <div id='err-inner-box'>
                <div id='err-img'>
                    <img src='/error.png'></img>
                </div>
                <div id='err-text'>
                    Error 404 : Page not found
                </div>
            </div>
        </div>
    )
}
export default Error;