import React, { useEffect, useState } from 'react'
import '../style/RecordForm.css'
import axios from 'axios'

function ExpenseForm({ userId, counter, result, setResult }) {
    const [listDisply, setDisplayList] = useState('none')
    const [catData, setCat] = useState([[], []])
    const [acData, setAc] = useState([[], []])
    const [date, setDate] = useState(new Date().setDate(new Date().getDate() + counter))
    const [userList, setUserList] = useState([])
    const [balance, setBalance] = useState()
    const [balance2, setBalance2] = useState()
    const [formData, setFormData] = useState({
        "userId": userId,
        "account": '',
        "title": '',
        "amount": '',
        "transactionDate": new Date(),
        "description": '',
        "category": '',
        "sentTo": "",
    });
    const handleInputChange = (e) => {
        const { name, value } = e.target;

        setFormData({ ...formData, [name]: value });
    };
    const handleUserList = (e) => {
        if(listDisply==='block')
            setDisplayList('none');
        else
            setDisplayList('block')

    };

    const handleSentTo = (e)=>{
        setFormData({...formData,sentTo:e.target.id})
    }

    useEffect(() => {
        axios.get('/api/account/name/' + userId,{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        })
            .then((doc) => {
                setAc(doc.data);
            }).catch((err) => {
                console.log(err);
            })
        axios.get('/api/EC',{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        })
            .then((doc) => {
                setCat(doc.data);
            }).catch((err) => {
                console.log(err);
            })
            axios.get('/api/user/'+userId,{
                headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
            })
            .then((doc) => {
                setFormData({...formData,account:doc.data.defaultAccount});
            }).catch((err) => {
                console.log(err);
            })
    }, [userId]);

    useEffect(() => {
        if (formData.account) {
            axios.get('/api/account/balance/' + userId + '/' + formData.account,{
                headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
            })
                .then((doc) => {
                    setBalance(Number(doc.data.currentBalance) - Number(formData.amount))
                }).catch((err) => {
                    console.log(err)
                })
        }
    }, [formData.account, formData.amount])

    // useEffect(() => {
    //     if (formData.sentTo) {
    //         axios.get('/user/' + formData.sentTo)
    //             .then((doc) => {
    //                 setDeAc(doc.data.defaultAccount)
    //             }).catch((err) => {
    //                 console.log(err)
    //             })
    //     }
    // }, [formData.sentTo])

    // useEffect(() => {
    //     if (formData.sentTo) {
    //         axios.get('http://localhost:8080/account/balance/' + formData.sentTo + '/' + deAc)
    //             .then((doc) => {
    //                 setBalance2(Number(doc.data.currentBalance) + Number(formData.amount))
    //             }).catch((err) => {
    //                 console.log(err)
    //             })
    //     }
    // }, [formData.amount, formData.sentTo])

    useEffect(() => {
        setDate(new Date().setDate(new Date().getDate() + counter))
        setFormData({
            ...formData,
            transactionDate: date
        })
    }, [counter])

    useEffect(()=>{
        if(formData.sentTo && formData.sentTo.length>=3){
            axios.get('/api/user/list/'+formData.sentTo,{
                headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
            })
            .then((doc)=>{
                setUserList(doc.data)
            }).catch((err)=>{
                console.log(err)
            })
        } else {
            setUserList([])
        }
    },[formData.sentTo])

    const handleSubmit = async () => {

        if (formData.account === '' || formData.amount === '' || formData.title === '') {
            setResult({ warning: 'Enter proper data', color: 'red' })
        } else if (balance < 0) {
            setResult({ warning: 'Insufficient Balance', color: 'red' })
        } else {
            setResult({warning:<div class="loader"></div>, color: 'white' })
            await axios.post('/api/expense', formData,{
                headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
            })
                .then((doc) => {
                    setResult({ warning: 'Expense Record added with transaction ID ' + doc.data._id, color: 'skyblue' })
                }).catch((err) => {
                    setResult({ warning: 'Unable to fetch!', color: 'red' })
                    console.log(err)
                })
        }
        setTimeout(() => {
            formData.amount = ''
            formData.title = ''
            formData.description = ''
            formData.category = ''
            formData.sentTo = ''
            setResult({ warning: '', color: 'skyblue' })
        }, 5000)
    }

    return (<>
        <div className='record-form'>
            <input required type='text' name='title' onChange={handleInputChange} className='form-field' placeholder='Enter Title' value={formData.title}></input>
            <input required type='number' name='amount' onChange={handleInputChange} className='form-field' placeholder='Amount' value={formData.amount}></input>
            <select required type='text' name='category' onChange={handleInputChange} className='form-field' value={formData.category}>
                <option className='option' value="">Select a Category</option>
                {catData.map((row) => {
                    return (<option key={row.categoryID} value={row.categoryID} className='option'>{row.categoryName}</option>)
                })}
            </select>
            <select required name='account' onChange={handleInputChange} className='form-field' value={formData.account}>
                <option className='option' value="">Select an Account</option>
                {acData.map((row) => {
                    return (<option key={row.accountId} value={row.accountId} className='option'>{row.accountName}</option>)
                })}
            </select>
            <input type='text' name='description' onChange={handleInputChange} className='form-field' placeholder='Description' value={formData.description}></input>
            <div id='user-list-box' onMouseEnter={()=>{setDisplayList('block')}} onFocus={()=>{setDisplayList('block')}} onMouseLeave={()=>{setDisplayList('none')}}  >
                <input type='text'id='sentTo' name='sentTo' onChange={handleInputChange}  className='form-field' placeholder='Sent To' value={formData.sentTo}></input>
                <div id='user-list' style={{display:listDisply}}  >
                    {(userList.length)?userList.map((curr)=>{
                        return <a className='user-list-btn' onClick={handleSentTo} id={curr.userId}>{curr.userName } <i>{curr.userId}</i></a>
                    }):<option className='user-list-btn' onClick={handleSentTo} value={''}><i>{'Enter atleast 3 characteres'}</i></option>}
                </div>
            </div>
            <button className='record-btn' placeholder='Recieved From' value='Submit' onClick={handleSubmit} ><img width='40px' src={'/public/submit.png'} alt='submit' /></button>
        </div>
        <div className='remark' style={{ color: result.color }}>{result.warning}</div></>
    )
}
export default ExpenseForm;