import React, { useEffect, useState } from "react";
import axios from "axios";
import "../style/Monthlyfinal.css";
import MonthlyIncome from "./MonthlyIncome";
import Monthlyexpense from "./Monthlyexpense";
function Monthlyfinal({ userId }) {
    const [inTotal, setInsum] = useState(0);
    const [exTotal, setExsum] = useState(0);
    const [monthYear, setMonth] = useState(String(new Date().getFullYear()) + '-' + String(new Date().getMonth() + 1));
    const handleMonthChange = (e) => {
        setMonth(e.target.value);
    }
    useEffect(() => {
        const [year, month] = monthYear.split('-').map((curr) => Number(curr));
        axios.post('/api/income/monthly/total', {
            userId, month, year
        },{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        }).then((doc) => {
            if (doc.data.length)
                setInsum(doc.data[0].total)
            else
                setInsum(0)

        })
        axios.post('/api/expense/monthly/total', {
            userId, month, year
        },{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        }).then((doc) => {
            if (doc.data.length)
                setExsum(doc.data[0].total)
            else
                setExsum(0)

        })
    }, [monthYear, userId])
    return (
        <>
            <div className="main">
                <div id='Yearly-records'>
                    <h2>Monthly Details</h2>
                </div>
                <div className="month-select" name="month">
                    <input type="month" id="month-inp" value={monthYear} onChange={handleMonthChange}></input>
                </div>
                <div className="ie">
                    <div className="component_name">
                        <div className="income">Income(Credit)<span id="incometot">{'₹ ' + inTotal}</span></div>
                        <MonthlyIncome monthYear={monthYear} userId={userId}  />
                    </div>
                    <div className="component">
                        <div className="expense">Expense(Debit)<span id="expensetot">{'₹ ' + exTotal}</span></div>
                        <Monthlyexpense monthYear={monthYear} userId={userId}  />
                    </div>
                </div>
            </div>
        </>
    );
}
export default Monthlyfinal;
