import React,{useEffect, useState} from 'react';
import axios from 'axios';

function SetAcc({userId}) {
    const [warn, setWarn] = useState('')
    const [acData, setAc] = useState([{}])
    const [deAc, setDeAc] = useState('')
    useEffect(()=>{
        axios.get('/api/user/'+userId,{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        })
        .then((doc)=>{
            setDeAc(doc.data.defaultAccount)
        }).catch((err)=>{
            console.log(err)
        })

        axios.get('/api/account/name/'+userId,{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        })
        .then((doc)=>{
            setAc(doc.data)
        }).catch((err)=>{
            console.log(err)
        })
    },[userId])
    const acChange=(e)=>{
        setDeAc(e.target.value)
    }
    const setDefaultAc = async ()=>{
        await axios.patch('/api/user/account',{id:userId,accId:deAc},{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        })
        .then((doc)=>{
            if(doc.data.userUpdated){
                setWarn({warning: 'Default Account Set to ' + deAc,color:'#6cc9ff'})
            } else {
                setWarn({warning: 'Error occuered! Try agian later...',color:'red'})
            }
        })
    }
    return (
        <div className='profile-box'>
            <div className='profile-title'>Set Default Account</div>
            <div className='add-ac-form'>
                <div className='record-form'>
                    <select required name='account' onChange={acChange} className='form-field' value={deAc}>
                        <option className='option' value="">Select an Account</option>
                        {acData.map((row) => {
                            return (<option key={row.accountId} value={row.accountId} className='option'>{row.accountName}</option>)
                        })}
                    </select>
                    <button className='record-btn' placeholder='Recieved From' value='Submit' onClick={setDefaultAc} ><img width='40px' src={'/public/submit.png'} alt='submit' /></button>
                </div>
                <div className='remark' style={{ color: warn.color }}>{warn.warning}</div>
            </div>
        </div>
    )
}
export default SetAcc;