import axios from 'axios'
import Yearly1 from "./Yearly1"
import Yearly2 from "./Yearly2"
import '../style/Yearly.css'
import { useEffect, useState } from 'react'

function Yearly({ userId }) {
    const [year, setYear] = useState(new Date().getFullYear())
    const [inTotal, setInTotal] = useState(0)
    const [exTotal, setExTotal] = useState(0)

    const handleInput = ((e) => {
        setYear(e.target.value)
    })

    useEffect(() => {
        axios.post('/api/income/yearly/total', {
            userId,
            year
        },{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        }).then((doc) => {
            if (doc.data.length)
                setInTotal(doc.data[0].total)
            else
                setInTotal(0)

        }).catch((err) => {
            console.log(err)
        });

        axios.post('/api/expense/yearly/total', {
            userId,
            year
        },{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        }).then((doc) => {
            if (doc.data.length)
                setExTotal(doc.data[0].total)
            else
                setExTotal(0)

        }).catch((err) => {
            console.log(err)
        });

    }, [year, userId])
    return (
        <div id='yearly-box'>
            <div id='Yearly-records'>
                <h2>Yearly Details</h2>
            </div>
            <div className="month-select">
                <input type="number" id="year-inp" value={year} onInput={handleInput}></input>
            </div>
            <div id='yearly-data-box'>
                <Yearly1 inTotal={inTotal} year={year} userId={userId}  />
                <Yearly2 exTotal={exTotal} year={year} userId={userId}  />
            </div>
        </div>
    )
}

export default Yearly
