import React from 'react';
import ReactDOM from 'react-dom/client';
import './style/index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { BrowserRouter } from 'react-router-dom';

const requestNotificationPermision = async () => {
    const permission = await Notification.requestPermission();

    if (permission != "granted") {
        throw new Error("Not granted");
    }
};

const ch4 = new BroadcastChannel("ch4");

if (localStorage.getItem('loginData')) {
    if (!("Notification" in window)) {
        console.log("No Notification");
    }else{
        requestNotificationPermision();
    }

    if (("serviceWorker" in navigator)) {
        const registration = await navigator.serviceWorker.register("sw.js", {
            scope: "./",
        });
        ch4.postMessage({ id: localStorage.getItem("loginData"), token:localStorage.getItem('token') });
    } else {
        console.log("No SW");
    }
}

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
    <React.StrictMode>
        <BrowserRouter>
            <App requestNotificationPermision={requestNotificationPermision} ch4={ch4}/>
        </BrowserRouter>
    </React.StrictMode>
);


// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
