import React, { useState, useEffect } from 'react'
import axios from 'axios';

function Yearly1({ inTotal, userId, year }) {
    const [inData, setData] = useState()
    const months = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December']
    useEffect(() => {
        axios.post('/api/income/yearly/get', {
            userId,
            year
        },{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        }).then((doc) => {
            setData(doc.data)
        })
    }, [userId, year]);
    return (
        <div id='yearly-income'>
            <div id='income-title'>Total Income<span id='in-total'>{'₹ ' + inTotal}</span></div>
            <div className='record-list'>
                {
                    (inData) ? months.map((curr, indx) => {

                        return (
                            <div className='income-container'>
                                <div className='yearly-record-line'>{curr}</div>
                                <div className='yearly-record-line amount'>{'₹ ' + ((inData[indx + 1]) || '0')}</div>
                            </div>
                        )

                    }) : <div className='no-record'>Unable to fetch!</div>
                }
                <div className='income-container total'>
                    <div className='yearly-record-line'>{'Total'}</div>
                    <div className='yearly-record-line amount' style={{color:'#16aa16'}}>{'₹ ' +inTotal}</div>
                </div>
            </div>
        </div>

    )
}

export default Yearly1
