import React, { useEffect, useState } from "react";
import Expense from "./Expense";
import Income from "./Income";
import DateBar from "./Date";
import EntryForm from './EntryForm';
import "../style/Daily.css";
import axios from "axios";

function Daily({ userId }) {
    const [counter, setCounter] = useState(0)
    const [inTotal, setInTotal] = useState(0)
    const [exTotal, setExTotal] = useState(0)
    const [result, setResult] = useState('')

    useEffect(() => {
        axios.post('/api/income/total',{userId,counter},{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        })
        .then((doc)=>{
            if(doc.data.length){
                setInTotal(doc.data[0].inTotal)
            } else {
                setInTotal(0)
            }
        })
        axios.post('/api/expense/total',{userId,counter},{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        })
        .then((doc)=>{
            if(doc.data.length){
                setExTotal(doc.data[0].inTotal)
            } else {
                setExTotal(0)
            }
        })
    }, [counter,result,userId])
    return (
        <>
            <EntryForm counter={counter} userId={userId} result={result} setResult={setResult} />
            <DateBar counter={counter} setCounter={setCounter} userId={userId} result={result} balance={inTotal-exTotal} />
            <div id="daily-box">
                <Income counter={counter} inTotal={inTotal} userId={userId} result={result}  />
                <Expense counter={counter} exTotal={exTotal} userId={userId} result={result} balance={+inTotal}  />
            </div>
        </>
    );
}
export default Daily;
