import React from 'react'
import { useState } from 'react';
import MonthlyChart from './MonthlyChart';
import YearlyChart from './YearlyChart';

function Chart({userId}) {
    const [inBtn, setInBtn] = useState(true)
    const [exBtn, setExBtn] = useState(false)
    const handleClick = (e) => {
        setInBtn(inBtn === true ? false : true)
        setExBtn(exBtn === true ? false : true)
    }

    return (
        <div id='entry-form-box'>
            <div id='menu-box'>
                <button className='menu-btn' id='income-btn' style={{ backgroundColor: (inBtn) ? '#1186934c' : '#464646' }} onClick={handleClick}>Monthly</button>
                <button className='menu-btn' id='expense-btn' style={{ backgroundColor: (exBtn) ? '#1186934c' : '#464646' }} onClick={handleClick}>Yearly</button>
            </div>
            
            {(inBtn)?<MonthlyChart userId={userId}  />:<YearlyChart userId={userId}  />}
        </div>
    )
}
export default Chart