import React from 'react'
import { NavLink } from 'react-router-dom';
import '../style/Mobile.css'

function Mobile({logout,handleClose }) {
    
  return (
    <div id='mobile-box'>
        <div id='mobile-close-btn'><a onClick={handleClose}>x</a></div>
        <div id='mobile-title'>MyExpenseManager</div>
        <div id='mobile-menu-box'>
            <NavLink to='/' onClick={handleClose}>Home</NavLink>
            <NavLink to='/monthly' onClick={handleClose}>Monthly</NavLink>
            <NavLink to='/yearly' onClick={handleClose}>Yearly</NavLink>
            <NavLink to='/charts' onClick={handleClose}>Charts</NavLink>
            <NavLink to='/profile' onClick={handleClose}>Profile</NavLink>
            <button onClick={logout}>Log Out</button>
        </div>
    </div>
  )
}
export default Mobile;