import React, { useState, useEffect } from "react";
import "../style/Date.css";
import "../style/Daily.css";

function DateBar({ counter, setCounter, userId, result ,balance }) {
    
    const next = () => {
        setCounter(counter + 1);
    };
    const prev = () => {
        setCounter(counter - 1);
    };

    let currDate = new Date();
    currDate.setDate(currDate.getDate() + counter);
    return (
        <div id="date-box">
            <div id="left-date-box">
                <button id="left-arrow" onClick={prev}>
                    ≺
                </button>
                <span id="date" style={{ border: (!counter) ? '2px solid white' : 'none', borderRadius: (!counter) ? '5px' : '0' }}>{currDate.toDateString()}</span>
            </div>
            <div id="right-date-box">
                <span id="daily-total">{"Total: ₹" + balance}</span>
                <button id="right-arrow" onClick={next}>
                    ≻
                </button>
            </div>
        </div>
    );
}
export default DateBar;
