import React, { useState, useEffect } from 'react'
import '../style/Expense.css'
import '../style/Record.css'
import '../style/Daily.css'
import axios from 'axios'

function Expense({ counter, exTotal, userId, result }) {
    const [exData, setData] = useState()
    const [loader, setLoader] = useState(<div class="loader"></div>)
    const [acData, setAc] = useState([{}])
    useEffect(() => {
        axios.get('/api/account/name/' + userId,{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        })
            .then((doc) => {
                setAc(doc.data);
            }).catch((err) => {
                console.log(err);
            })
    }, [userId])

    useEffect(() => {
        setLoader(<div class="loader"></div>)
        axios.post('/api/expense/get', { userId, counter },{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        })
            .then((doc) => {
                setData(doc.data)
                setLoader('')
            }).catch((err) => {
                setLoader('Unable to fetch!')
                console.log(err)
            })
    }, [counter, result, userId]);

    return (
        <div id='expense-box'>
            <div id='expense-title'>Total Expense<span id='ex-total'>{'₹ ' + exTotal}</span></div>
            <div className='record-list'>{(exData === undefined) ? <div className='no-record'>{loader}</div> : (exData.length > 0) ? exData.map((row) => {

                return (
                    <div key={row[0]} className='record'>
                        <div className='record-left'>
                            <span className='record-first-line'>
                                <img src={row.category || '/category/EC0.png'} className='record-img' alt={row.category} />
                                <span className='record-title'>{row.title}</span>
                                <span className='record-ac'>{acData.map((curr) => {
                                    if (curr.accountId === row.account)
                                        return curr.accountName
                                })}</span>
                            </span>
                            <div className='record-desc'>{row.description}</div>
                        </div>
                        <div>
                            <div className='record-amount'>{'₹ ' + row.amount}</div>
                            <div className='record-to'>{(row.sentTo) ? 'to ' + row.sentTo : ''}</div>
                        </div>
                    </div>
                )
            }) : <div className='no-record'>Enter Expense Record</div>}
            </div>
        </div>
    )
}
export default Expense;