import React, { useEffect, useState } from 'react'
import axios from 'axios';
import { PieChart, Pie, Cell, Tooltip, Legend } from "recharts";
import '../style/Chart.css'

function YearlyChart({ userId }) {
    const [inp, setMonth] = useState(new Date().getFullYear());
    const [inData, setInData] = useState([]);
    const [exData, setExData] = useState([]);
    const [inTotal, setInTotal] = useState(1);
    const [exTotal, setExTotal] = useState(1);

    const handleMonthChange = (e) => {
        setMonth(e.target.value);
    }

    useEffect(() => {
        axios.post('/api/income/charts/yearly', {
            userId, year:inp
        },{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        }).then((doc) => {
            setInData(doc.data)
        }).catch((err) => {
            console.log(err)
        })
        axios.post('/api/expense/charts/yearly', {
            userId, year:inp
        },{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        }).then((doc) => {
            setExData(doc.data)
        }).catch((err) => {
            console.log(err)
        });
        axios.post('/api/income/yearly/total', {
            userId, year:inp
        },{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        }).then((doc) => {
            setInTotal(doc.data[0].total)
        }).catch((err) => {
            console.log(err)
        });
        axios.post('/api/expense/yearly/total', {
            userId, year:inp
        },{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        }).then((doc) => {
            setExTotal(doc.data[0].total)
        }).catch((err) => {
            console.log(err)
        });
    }, [userId, inp])

    const CustomTooltip = ({ active, payload, label }) => {
        if (active) {
            return (
                <div
                    className="custom-tooltip"
                    style={{
                        backgroundColor: "#000",
                        padding: "5px",
                        border: "1px solid #cccc"
                    }}
                >
                    <label>{`${payload[0].name} : ₹ ${payload[0].value}`}</label>
                </div>
            );
        }
        return null;
    };
    return (
        <div id='entry-box'>
            <div id='month-inp-box'>
                <input type="number" id="month-inp" value={inp} onChange={handleMonthChange}></input>
            </div>
            <div className='chart-box'>
                <div className='chart'>
                    <div className='chart-title'>Income</div>
                    {
                        (inData.length) ?
                            <PieChart width={400} height={300}>
                                <Pie
                                    data={inData}
                                    color="black"
                                    dataKey="value"
                                    nameKey="title"
                                    cx="50%"
                                    cy="50%"
                                    outerRadius={120}
                                    fill="black"
                                >
                                    {inData.map((entry, index) => (
                                        <Cell
                                            key={`cell-${index}`}
                                            fill={entry.iconColor}
                                        />
                                    ))}
                                </Pie>
                                <Tooltip content={<CustomTooltip />} />
                                <Legend />
                            </PieChart>
                            : <div>No Data</div>
                    }
                    <div className='chart-data'>
                        {
                            inData.map((curr)=>{
                                return (
                                    <div className='chart-entry'>
                                        <img className='chart-entry-img' src={curr.iconURL} width={'30px'}></img>
                                        <div className='chart-entry-title'>{`${curr.title}: ₹${curr.value} (${Math.round(10000*curr.value/inTotal)/100}%)`}</div>
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>
                <div className='chart'>
                    <div className='chart-title'>Expense</div>
                    {
                        (exData.length) ?
                            <PieChart width={400} height={300}>
                                <Pie
                                    data={exData}
                                    color="black"
                                    dataKey="value"
                                    nameKey="title"
                                    cx="50%"
                                    cy="50%"
                                    outerRadius={120}
                                    fill="black"
                                >
                                    {exData.map((entry, index) => (
                                        <Cell
                                            key={`cell-${index}`}
                                            fill={entry.iconColor}
                                        />
                                    ))}
                                </Pie>
                                <Tooltip content={<CustomTooltip />} />
                                <Legend />
                            </PieChart>
                            : <div>No Data</div>
                    }
                    <div className='chart-data'>
                        {
                            exData.map((curr)=>{
                                return (
                                    <div className='chart-entry'>
                                        <img className='chart-entry-img' src={curr.iconURL} width={'30px'}></img>
                                        <div className='chart-entry-title'>{`${curr.title}: ₹${curr.value} (${Math.round(10000*curr.value/exTotal)/100}%)`}</div>
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>
            </div>
        </div>
    )
}
export default YearlyChart;