import React, { useState, useEffect } from 'react';
import '../style/Monthly.css';
import '../style/RecordForm.css';
import '../style/Monthlyexpense.css'
import axios from 'axios';

function Monthlyexpense({ userId,monthYear}) {
    const [monData, setData] = useState();
    const [keys, setKeys] = useState();
    let sum = 0;

    useEffect(()=>{
        const [year, month] = monthYear.split('-').map((curr) => Number(curr));
        axios.post('/api/expense/monthly/get', {
            userId, month, year
        },{
            headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
        }).then((doc) => {
            setData(doc.data)
            setKeys(Object.keys(doc.data))
        }).catch(err=>{
            console.log(err)
        })
    },[userId,monthYear]);

    return (
        <div id='monthly-box'>
            {(monData) ? (keys.length) ? (keys.map((curr) => {
                sum = 0;
                return (
                    <div className="income_display" key={curr}>
                        <div className="Income">
                            <div className="date">{new Date(monData[curr][0].transactionDate).toLocaleDateString()}</div>
                            {monData[curr].map((i) => {
                                sum = (sum + i.amount) 
                                return <div className="title_amount" key={i._id}>{i.title}<span id="amount" style={{ color: 'white' }}>{'₹ ' + i.amount}</span></div>
                            })}
                            <div className="title_amount sum" style={{color:'rgb(255, 47, 0)'}}>Total: <span id="amount" style={{color:'rgb(255, 47, 0)'}}>{'₹ ' + sum}</span></div>
                        </div>
                    </div>
                )
            })) : <div className='no-record'>No Expense Record Found</div> : <div className='no-record'>Unable to fetch</div>}
        </div>
    );
}

export default Monthlyexpense;
