import React, { useEffect, useState } from 'react';
import axios from 'axios';

function AddAcc({ userId, }) {
    const [warn, setWarn] = useState('')
    const [formData, setFormData] = useState({
        "userId": userId,
        "accountName": '',
        "accountDesc": '',
        "openingBalance": '',
        "currentBalance": '',
    });

    const handleInputChange = (e) => {
        const { name, value } = e.target;

        setFormData({ ...formData, [name]: value, currentBalance: formData.openingBalance });
    };

    // useEffect(()=>{
    //     axios.get(+'/account/count/' + userId)
    //         .then((doc) => {
    //             setFormData({ ...formData, accountId: 'A' + (Number(doc.data.count) + 1)});
    //         }).catch((err) => {
    //             console.log(err)
    //         })
    // },[userId])
    
    const handleSubmit = async () => {
        if (formData.name === '' || formData.opening === '') {
            setWarn({ warning: 'Enter proper data', color: 'red' })
        } else {
            await axios.post('/api/account', formData,{
                headers:{'Authorization':  'Bearer '+localStorage.getItem('token')}
            })
                .then((doc) => {
                    setFormData({ ...formData, accountOpeningDate: new Date() });
                    setWarn({ warning: 'Account added successfully.', color: '#6cc9ff' });
                }).catch((err) => {
                    console.log(err);
                })
        }
        setTimeout(() => {
            setFormData({
                "userId": userId,
                "accountId": '',
                "accountName": '',
                "accountDesc": '',
                "openingBalance": '',
                "currentBalance": '',
                "accountOpeningDate": ''
            })
            setWarn('')
        }, 10000)
    }

    return (
        <div className='profile-box'>
            <div className='profile-title'>Add Account</div>
            <div className='add-ac-form'>
                <div className='record-form'>
                    <input required type='text' name='accountName' onChange={handleInputChange} className='form-field' placeholder='Account Name' value={formData.accountName}></input>
                    <input required type='number' name='openingBalance' onChange={handleInputChange} className='form-field' placeholder='Opening Balance' value={formData.openingBalance}></input>
                    <input type='text' name='accountDesc' onChange={handleInputChange} className='form-field' placeholder='Description' value={formData.accountDesc}></input>
                    <button className='record-btn' placeholder='Recieved From' value='Submit' onClick={handleSubmit} ><img width='40px' src={'/public/submit.png'} alt='submit' /></button>
                </div>
                <div className='remark' style={{ color: warn.color }}>{warn.warning}</div>
            </div>
        </div>
    )
}
export default AddAcc;